import React, {useState, useEffect} from 'react';
import * as api from '../../../api/api';
const Leauges = ({leagueEntriesInfo}) => {
    const [solorankinfo, setSolorankinfo] = useState('');
    const [tftrankinfo, setTftrankinfo] = useState('');
    const [teamrankinfo, setTeamankinfo] = useState('');
    useEffect(() => {
        if(leagueEntriesInfo) {
            leagueEntriesInfo.map(v => {
                api.getleagueSecond(v.leagueId)
                .then(response => {
                    console.log(response.data.queue)
                    if(response.data.queue === "RANKED_SOLO_5x5"){
                        setSolorankinfo(response.entries);
                    } else if(response.data.queue === "RANKED_FLEX_SR"){
                        setTeamankinfo(response.entries);
                    }
                })
                .catch(error => console.log(error))
            })
        }
    },[leagueEntriesInfo])
    console.log(solorankinfo, teamrankinfo)
    return (
        <div>
            {
                
            }
        </div>
    );
}

export default Leauges;